import React from 'react';

const About = () => {
    return(
        <div>
            <h1 className="ui header">About</h1>
            <div className="ui middle aligned two column centered grid">
                <div className="column">
                    <div className="row">
                        <p>
                        I'm a professional with skills in computational optimization and programming. 
                        Interested in algorithms for optimization under uncertainty and machine learning 
                        to deliver solutions for a range of industries. I have skills using data 
                        engineering tools for leveraging analytics and machine learning solutions. 
                        Experienced in education 
                        </p>
                        <p>Strong familiarity with the following:</p>
                        <ul>
                            <li>Scientific computing: Theory, algorithms and tools for optimization and machine learning</li>
                            <li>Machine Learning: Spark ML, BigQuery ML, sklearn, PyTorch</li>
                            <li>Data analysis tools: pandas, matplotlib, numpy, plotly, SQL</li>
                            <li>Data engineering frameworks: Airflow, PySpark, Kafka, Dataproc, Dataflow, BigQuery</li>
                            <li>Cloud infrastructure platforms: AWS and GCP</li>
                            <li>Web development: Javascript and ReactJS</li>
                        </ul>
                    </div>
                    <div className="row">
                        <div className="ui divider"></div>
                    </div>
                    <div className="row">
                        <div className="row">
                            <h3 className="ui header">Interests</h3>
                        </div>
                        <div className="ui hidden divider"></div>
                        <div className="row">
                            <div className="ui bulleted list">
                                <div className="item">
                                    Optimization under Uncertainty
                                </div>
                                <div className="item">
                                    Deep Reinforcement Learning
                                </div>
                                <div className="item">
                                    Sequential Decision Making
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="ui divider"></div>
                    </div>
                    <div className="row">
                        <h3 className="ui header">Publications</h3>
                        <div className="ui hidden divider"></div>
                        <div className="row">
                            <div className="ui bulleted list">
                            <div className="item">
                                    <div className="header">
                                    Inventory Control Under Unobserved Losses with Latent State Learning
                                    </div>
                                    Jose Manuel Vera
                                    <div className="description">
                                        <em>7th International Conference on Computer and Communications (ICCC)</em>, 2021. [<a href="https://ieeexplore.ieee.org/abstract/document/9674290">
                                        link
                                    </a>]
                                    </div> 
                                </div>
                                <div className="item">
                                    <div className="header">
                                        Application of the Cross-Entropy Method to the Dynamic Assortment Optimization Problem
                                    </div>
                                    Jose Manuel Vera
                                    <div className="description">
                                        <em>Matemática</em>, vol. 18, no. 1, 2020. [<a href="http://www.revistas.espol.edu.ec/index.php/matematica/article/view/696">
                                        link
                                    </a>]
                                    </div> 
                                </div>
                                <div className="item">
                                    <div className="header">
                                        Deep Reinforcement Learning for Routing a Heterogeneous Fleet of Vehicles
                                    </div>
                                    Jose Manuel Vera, Andres G. Abad
                                    <div className="description">
                                        <em>IEEE Latin American Conference on Computational Intelligence (LA-CCI)</em>, 2019.  [<a href="https://ieeexplore.ieee.org/abstract/document/9037042">
                                    link
                                    </a>]
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div className="column">
                    
                    <img alt="railway" className="ui medium rounded image" src={require("../images/photo_2.jpg")}/>
                </div>
            </div>
        </div>
    )
}

export default About;