import React from 'react';
import { Image } from 'semantic-ui-react'
import './Home.css';

const Home = () =>{
    return(
        <div className="ui grid">
            <div className="row">
                <div className="ui centered Big image">
                    <img alt="background" src={require("../images/bg_img_3.jpg")}/>
                </div>
            </div>
            <div className="row">
                <div className="ui column stackable grid">
                    <div className="three wide column">
                        <Image src={require("../images/profile_img.jpg")} size='small' circular />
                    </div>
                    <div className="seven wide column">
                        <h2>José Manuel Vera</h2>
                        <em>Industrial Engineer &#8226; Computational Optimization &#8226; Data Engineer &#8226; Developer</em>
                        <hr></hr>
                        <p><i className="envelope icon"></i>josemanuel[dot]vera[dot]aray[at]gmail[dot]com</p>
                        <p><i className="github icon"></i>github.com/jomavera</p>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Home;